.main {
    width: 100%;
    height: 100vh;
    padding: 20px 30px;
    display: grid;
    grid-template-columns: minmax(450px, 570px) minmax(820px, 1800px) 350px;
    grid-column-gap: 20px;
}

.left, .right {
    & > div {
        background: var(--primary-second);
        border-radius: 16px;
    }
}
.middle {
    position: relative;
    overflow: auto;
    background: var(--primary-second);
    border-radius: 16px;
    &::-webkit-scrollbar {
        width: 18px;
    }
    &::-webkit-scrollbar-track {
        background: var(--primary-second);
        border-radius: 40px;
      }
    &::-webkit-scrollbar-thumb {
        background-color: rgba(67, 78, 97, .3);
        border: 6px solid var(--primary-second);
        border-radius: 40px;
    }
}
.left {
    display: grid;
    grid-template-rows: 220px 1fr;
    grid-row-gap: 20px;
}
.right {
    display: grid;
    grid-template-rows: 330px 195px 1fr;
    grid-row-gap: 20px;
}
.sidebar {
    display: none;
    background: var(--primary-second);
    border-radius: 16px;
    height: calc(100vh - 40px);
}